import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    urlId: Number(sessionStorage.getItem("urlId"))
  },
  getters: {
    urlId: () => Number(sessionStorage.getItem("urlId"))
  },
  mutations: {
    SET_URLID(state, urlId) {
      state.urlId = urlId;
      sessionStorage.setItem("urlId", urlId)
    },
  },
  actions: {
  },
  modules: {
  }
})
