import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: () => import('../views/home/index.vue')
  },
  {
    path: '/overview',
    name: 'overview',
    component: () => import('../views/overview/index.vue')
  },
  {
    path: '/projection',
    name: 'projection',
    component: () => import('../views/projection/index.vue')
  },
  {
    path: '/labour',
    name: 'labour',
    component: () => import('../views/labour/index.vue')
  },
  {
    path: '/economy',
    name: 'economy',
    component: () => import('../views/economy/index.vue')
  },
  {
    path: '/poverty',
    name: 'poverty',
    component: () => import('../views/poverty/index.vue')
  },
  {
    path: '/:catchAll(.*)',
    name: 'error',
    component: () => import("../views/error/404.vue")
  }

]

const router = new VueRouter({
  routes
})

export default router
