<template>
  <div id="app">
    <div class="home" v-if="isShow">
      <div class="home-left">
        <leftMenu />
      </div>

      <div class="home-right">
        <router-view />
      </div>
    </div>
    <router-view v-else />
  </div>
</template>

<script>
// @ is an alias to /src
import leftMenu from '@/components/menu/index'

export default {
  name: 'app',
  components: {
    leftMenu
  },
  data() {
    return {
      isShow: true
    }
  },
  watch: {
    $route: {
      handler(val, oldval) {
        if (val.name == "error") this.isShow = false
        else this.isShow = true
      },
      deep: true
    },
  }
}
</script>

