<template>
	<div class="menu">
		<div class="menu-logo">
			<img class="menu-logo-img" src="@/assets/logo.png" />
			<div>
				CityOverview
			</div>
		</div>
		<p class="menu-p">HK.</p>
		<div class="menu-list">
			<div class="menu-list-item" v-for="(item, index) in list" :key="index">
				<p class="menu-list-item-p">{{ item.name }}</p>
				<div :class="['menu-list-item-children', activity === it.id ? 'activity' : '']"
					v-for="(it, ind) in item.children" :key="ind" @click="click(it)">
					<span :class="['iconfont','leftIcon', activity === it.id ? it.selectIcon : it.icon]" style="margin-right: 5px"></span>
					{{ it.name }}
				</div>
			</div>
		</div>
	</div>
</template>


<script>

export default {
	name: 'index.vue',
	data() {
		return {
			list: [
				{
					name: "MAIN",
					children: [
						{
							id: 1,
							name: "City at a Glance",
							url: 'home',
							icon: "icon-fenlei",
							selectIcon: 'icon-yingyong'
						}
					]
				},
				{
					name: "DETAILS",
					children: [
						{
							id: 2,
							name: "Population Overview",
							url: 'overview',
							icon: "icon-weibiaoti-",
							selectIcon: 'icon-weibiaoti4'
						}, {
							id: 3,
							name: "Population Projection",
							url: 'projection',
							icon: "icon-fenlei",
							selectIcon: 'icon-yingyong'
						},
						{
							id: 4,
							name: "Labour",
							url: 'labour',
							icon: "icon-dingdan",
							selectIcon: 'icon-dingdan-'
						},
						{
							id: 5,
							name: "Economy",
							url: 'economy',
							icon: "icon-24gl-tags",
							selectIcon: 'icon-biaoqian'
						}, {
							id: 6,
							name: "Poverty Situation",
							url: 'poverty',
							icon: "icon-wenjianjia",
							selectIcon: 'icon-24gf-folderOpen'
						},
					]
				},
			],
			activity: 0
		}
	},
	created() {
		this.activity = this.$store.state.urlId;
	},
	methods: {
		click(it) {
			if (this.activity === it.id) return
			this.activity = it.id
			this.$store.commit('SET_URLID', it.id);
			this.$router.push({ name: it.url })
		}
	}
}
</script>


